<template>
<div class="container container-sm">
    <form action="" method="POST" @submit.prevent='submitForm()'>
        <h2 class="mb-4">ng k</h2>

        <transition name="fade-in">
            <div class="log" v-show="log" :class="isSuccess ? 'success' : 'error'">
                <p>{{ apiLog }}</p>
                <p>{{ log }}</p>
            </div>
        </transition>

        <label class="input">
            <span class="material-icons">label</span>
            <input type="text" name="name" required placeholder="Tn hin th" v-model="form.name">
            <p>Tn hin th</p>
        </label>

        <label class="input">
            <span class="material-icons">person</span>
            <input type="text" name="username" required placeholder="Tn ng nhp" v-model="form.username">
            <p>Tn ng nhp</p>
        </label>

        <label class="input">
            <span class="material-icons">email</span>
            <input type="email" name="email" required placeholder="Email" v-model="form.email">
            <p>Email</p>
        </label>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" name="password" required placeholder="Mt khu" v-model="form.password">
            <p>Mt khu</p>
        </label>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" name="repassword" required placeholder="Nhp li mt khu" v-model="form.repassword">
            <p>Re-password</p>
        </label>

        <div class="footer">
            <button class="btn btn-primary">
                <span class="material-icons mr-2">send</span>
                ng k
            </button>

            <div>
                <p> c ti khon? <router-link to="/login">[ng Nhp]</router-link></p>
                <p><router-link to="">[Qun Mt Khu]</router-link></p>
            </div>
        </div>
    </form>
</div>
</template>

<style scoped>
.log {
    padding: .25rem 1rem;
    border-radius: .25rem;
    font-size: 12px;
}

.error {
    color: red;
    background-color: #ff000010;
}

.success {
    color: green;
    background-color: #00ff0010;
}
</style>

<script>
import api from '/src/assets/js/api'
import store from '/src/assets/js/store'

export default {
    name: 'RegisterView',

    data () {
        return {
            data: store.getAll(),

            form: {
                name: '',
                username: '',
                email: '',
                password: '',
                repassword: ''
            },

            apiLog: '',
            log: '',
            isSuccess: false
        }
    },
    
    methods: {
        submitForm () {
            if (this.form.password !== this.form.repassword) {
                this.log = 'Mt khu khng khp'
                this.isSuccess = false
                return
            }

            this.data.isLoading = true

            api.post('/register', this.form)
            .then(res => {
                this.data.isLoading = false

                if (res.status === 200) {
                    res.json().then(data => {
                        this.isSuccess = true
                        this.apiLog = '';
                        this.log = "ng k thnh cng"
                        store.set('user', data.user)
                        store.set('isLogin', true)
                        api.setAvatarURL()
                        this.$router.push('/')
                    })
                }
                else {
                    res.json().then(data => {
                        this.isSuccess = false
                        this.apiLog = data.error
                        this.showLog()
                    })
                }
            })
            .catch(err => {
                this.data.isLoading = false
                
                console.log(err)
            })
        },

        showLog () {
            if (this.apiLog == 'Invalid name') this.log = 'Tn phi ln hn 2 v nh hn 50 k t'
            else if (this.apiLog == 'Invalid email') this.log = 'Email khng ng nh dng'
            else if (this.apiLog == 'Invalid username') this.log = 'Tn ng nhp phi t 3 n 20 k t, khng bao gm khong trng v k t c bit'
            else if (this.apiLog == 'Invalid password') this.log = 'Mt khu phi ln hn 6 k t, bao gm ch ci vit thng, vit hoa, s v k t c bit'
            else if (/(ER_DUP_ENTRY: Duplicate entry ').+(' for key 'username')/gi.test(this.apiLog)) this.log = 'Tn ng nhp  tn ti'
            else if (/(ER_DUP_ENTRY: Duplicate entry ').+(' for key 'email')/gi.test(this.apiLog)) this.log = 'Email  tn ti'
            else this.log = ' c li xy ra'
        }
    }
}
</script>