<template>
<div class="container container-sm">
    <div class="breadcrumb">
        <router-link to="/profile">C Nhn</router-link>
        <router-link to="/profile/edit">Sa Thng Tin C Nhn</router-link>
    </div>

    <form action="" @submit.prevent="updateAvatar()">
        <img :src="avatarPreview" :alt="data.user.username" class="d-block mx-auto avatar-xl" />
        <br>

        <p>Cp nht nh i din</p>

        <label class="input">
            <input type="file" @change="onchangeAvatar">
        </label>

        <button class="btn btn-primary">
            <span class="material-icons mr-2">file_upload</span>
            Upload
        </button>
    </form>
    <hr>

    <form action="" @submit.prevent="updateProfile()">
        <p>Cp nht thng tin c nhn</p>

        <label class="input">
            <span class="material-icons">label</span>
            <input type="text" v-model="form.name" placeholder="Tn hin th" />
            <p>Tn hin th</p>
        </label>

        <label class="input">
            <span class="material-icons">favorite</span>
            <input type="text" v-model="form.slogan" placeholder="Chm ngn" />
            <p>Chm ngn</p>
        </label>

        <button class="btn btn-primary">
            <span class="material-icons mr-2">save</span>
            Save
        </button>
    </form>
    <hr>

    <form action="" @submit.prevent="updatePassword()">
        <p>Cp nht mt khu</p>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" v-model="passwordForm.oldPassword" placeholder="Mt khu c" />
            <p>Mt khu c</p>
        </label>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" v-model="passwordForm.newPassword" placeholder="Mt khu mi" />
            <p>Mt khu mi</p>
        </label>

        <label class="input">
            <span class="material-icons">lock</span>
            <input type="password" v-model="rePassword" placeholder="Xc nhn mt khu" />
            <p>Xc nhn mt khu</p>
        </label>

        <button class="btn btn-primary">
            <span class="material-icons mr-2">save</span>
            Save
        </button>
    </form>

    <message-popup
    :title="message.title"
    :message="message.message"
    :type="message.type"
    :is-display="message.isDisplay" />
</div>
</template>

<script>
import store from '@/assets/js/store'
import api from '@/assets/js/api'
import MessagePopup from '@/components/MessagePopup.vue'

export default {
    name: 'EditProfileView',

    components: {
        MessagePopup
    },

    data () {
        return {
            data: store.getAll(),
            
            form: {
                name: store.get('user').name,
                slogan: store.get('user').slogan
            },

            passwordForm: {
                oldPassword: '',
                newPassword: ''
            },

            rePassword: '',

            avatarPreview: api.getAvatarURL(store.get('user').username),
            avatarFile: null,

            isLoading: false,

            message: {
                title: '',
                message: '',
                type: '',
                isDisplay: false
            }
        }
    },

    methods : {
        updateAvatar () {
            if (!this.avatarFile) {
                this.showMessage(
                    'Li',
                    'Vui lng chn nh i din',
                    'error'
                )

                return
            }

            let data = new FormData()
            data.append('avatar', this.avatarFile)

            this.data.isLoading = true

            api.updateAvatar(data)
            .then(res => {
                this.data.isLoading = false

                if (res.status == 200) {
                    location.reload()
                }
                else {
                    this.showMessage(
                        'Li!',
                        'C li xy ra, vui lng th li',
                        'error'
                    )
                }
            })
            .catch(err => {
                this.data.isLoading = false

                console.log(err)

                this.showMessage(
                    'Li!',
                    'Khng th kt ni n my ch',
                    'error'
                )
            })
        },

        updateProfile () {
            if (!this.form.name || !this.form.slogan) {
                this.showMessage(
                    'Li',
                    'Vui lng nhp y  thng tin',
                    'warning'
                )

                return
            }

            this.data.isLoading = true

            api.put('/user', this.form)
            .then(res => {
                this.data.isLoading = false

                if (res.status == 200) {
                    res.json().then(data => {
                        this.data.user = data.user

                        this.showMessage(
                            'Thnh cng',
                            'Cp nht thng tin c nhn thnh cng',
                            'success'
                        )
                    })
                }
                else {
                    res.json().then(data => {
                        console.log(data.error)

                        let msg = ''

                        if (data.error == 'Invalid name') msg = 'Tn hin th phi ln hn 2 v nh hn 50 k t'
                        else msg = 'C li xy ra, vui lng th li'

                        this.showMessage(
                            'Li!',
                            msg,
                            'error'
                        )
                    })
                }
            })
            .catch(err => {
                this.data.isLoading = false

                console.log(err)

                this.showMessage(
                    'Li!',
                    'Khng th kt ni n my ch',
                    'error'
                )
            })
        },

        updatePassword () {
            if (!this.passwordForm.oldPassword || !this.passwordForm.newPassword) {
                this.showMessage(
                    'Li',
                    'Vui lng nhp y  thng tin',
                    'warning'
                )

                return
            }

            if (this.passwordForm.newPassword != this.rePassword) {
                this.showMessage(
                    'Li',
                    'Mt khu mi v xc nhn mt khu khng khp',
                    'warning'
                )

                return
            }

            this.data.isLoading = true

            api.put('/user/password', this.passwordForm)
            .then(res => {
                this.data.isLoading = false

                if (res.status == 200) {
                    res.json().then(() => {
                        this.showMessage(
                            'Thnh cng',
                            'Cp nht mt khu thnh cng',
                            'success'
                        )
                    })

                    this.$router.push('/profile')
                }
                else {
                    res.json().then(data => {
                        console.log(data.error)

                        let msg = ''

                        if (data.error == 'Invalid password') msg = 'Mt khu phi t 6 k t, bao gm ch in hoa, ch in thng, s v k t c bit'
                        else if (data.error == 'Old password is incorrect') msg = 'Mt khu c khng chnh xc'
                        else msg = 'C li xy ra, vui lng th li'

                        this.showMessage(
                            'Li!',
                            msg,
                            'error'
                        )
                    })
                }
            })
            .catch(err => {
                this.data.isLoading = false
                
                console.log(err)

                this.showMessage(
                    'Li!',
                    'Khng th kt ni n my ch',
                    'error'
                )
            })
        },

        onchangeAvatar (e) {
            let files = e.target.files || e.dataTransfer.files

            if (!files.length) return

            this.convertAvatar(files[0])
        },

        // Convert avatar to 256x256 png file
        convertAvatar (avatar) {
            let canvas = document.createElement('canvas')
            let ctx = canvas.getContext('2d')
            let img = document.createElement('img')
            let scale = 256
            
            canvas.width = scale
            canvas.height = scale
            
            img.src = URL.createObjectURL(avatar)

            img.onload = () => {
                URL.revokeObjectURL(img.src)
                
                if (img.width > img.height) {
                    let s = img.height / scale
                    let w = img.width / s
                    
                    ctx.drawImage(img, 0, 0, w, scale)
                }
                else {
                    let s = img.width / scale
                    let h = img.height / s
                    
                    ctx.drawImage(img, 0, 0, scale, h)
                }
                
                this.avatarPreview = canvas.toDataURL()

                fetch(this.avatarPreview).then(res => {
                    res.arrayBuffer().then(buf => {
                        this.avatarFile = new File([buf], 'avatar', {type: 'image/png'})
                    })
                })
            }
        },

        showMessage (title, message, type) {
            this.message.title = title
            this.message.message = message
            this.message.type = type
            this.message.isDisplay = !this.message.isDisplay
        }
    }
}
</script>